@import '~assets/styles/h3Imports';

.root {
  border: em(1) solid var(--gray-3);
  border-radius: em(8);
  color: var(--gray-3);
  height: em(24);
  padding: em(24);
  width: 100%;

  &:read-only {
    background-color: var(--gray-0, #f1f3f3);
    border-color: #a5aaaa;
    font-weight: 600;
  }

  &[data-validity='invalid'] {
    border-color: var(--input-control-validity-color);
    outline-color: var(--input-control-validity-color);

    :focus-visible {
      outline-color: var(--input-control-validity-color);
    }
  }

  &:focus-visible {
    outline-offset: 0;
  }

  &[data-validity='invalid']:enabled:hover {
    outline: em(1) solid var(--input-control-validity-color);
    outline-offset: -1px;
    outline-width: 2px;
  }

  &:enabled:hover {
    outline: em(1) solid var(--input-hover-outline-color, #414646);
    outline-offset: -1px;
    outline-width: 2px;

    &:focus-visible {
      &:valid {
        border-color: var(--input-outline-color);
        outline-color: var(--input-outline-color, #414646);
      }
    }

    --input-border-color: var(--input-outline-color, #414646);
  }

  @media (max-width: $max-phone-size) {
    width: calc(100% - #{em(4)});
  }

  &::placeholder {
    color: var(--gray-3);
    opacity: 1;
  }
}
