@import '../utils';

.root {
  [data-element='input-wraper'] {
    position: relative;
  }

  [data-element='input-wraper'] input {
    padding-right: em(50);
    width: 100%;
  }

  [data-element='input-type-toggle'] {
    position: absolute;
    right: em(1.5);
    top: 50%;
    transform: translateY(-50%);
  }
}
