@mixin variant-inverted {
  &[data-variant='inverted'] {
    --button-background-color: var(--button-inverted-background-color, white);
    --button-color: var(--button-inverted-color, #282b2b);
    --button-hover-background-color: var(
      --button-inverted-hover-background-color,
      #a5aaaa
    );
    --button-hover-color: var(--button-inverted-hover-color, white);
    --button-hover-border-color: var(
      --button-inverted-hover-border-color,
      white
    );
    --button-active-background-color: var(
      --button-inverted-active-background-color,
      #414646
    );
    --button-active-border-color: var(
      --button-inverted-active-border-color,
      white
    );
    --button-active-color: var(--button-inverted-active-color, white);
  }
}

@mixin variant-inverted-outlined {
  &[data-variant='inverted-outlined'] {
    --button-background-color: var(--button-inverted-background-color, none);
    --button-border-color: var(--button-inverted-border-color, white);
    --button-color: var(--button-inverted-color, white);
    --button-hover-background-color: var(
      --button-inverted-hover-background-color,
      #a5aaaa
    );
    --button-hover-color: var(--button-inverted-hover-color, white);
    --button-hover-border-color: var(
      --button-inverted-hover-border-color,
      white
    );
    --button-active-background-color: var(
      --button-inverted-active-background-color,
      #414646
    );
    --button-active-border-color: var(
      --button-inverted-active-border-color,
      white
    );
    --button-active-color: var(--button-inverted-active-color, white);
    --button-border-width: 2px;
  }
}
