@import '../../utils';

.root {
  background-color: var(--segmented-input-background-color, none);
  border-color: var(
    --segmented-input-border-color,
    var(--segmented-input-background-color)
  );
  border-radius: var(--segmented-input-border-radius, em(50));
  border-style: solid;
  border-width: var(--segmented-input-border-width, #{em(2)});
  color: var(--segmented-input-color);
  flex: 1;
  font-size: var(--segmented-input-font-size);
  font-weight: var(--segmented-input-font-weight, 700);
  line-height: var(--segmented-input-line-height);
  min-width: var(--segmented-input-min-width);
  transition: background-color 0.2s, color 0.2s, border-color 0.2s;

  label {
    align-items: center;
    display: flex;
    flex-grow: 1;
    justify-content: center;
    padding: var(--segmented-input-padding, #{em(13)} #{em(25)});
  }

  &:not(:first-child) {
    border-bottom-left-radius: 0;
    border-left: none;
    border-top-left-radius: 0;
  }

  &:not(:last-child) {
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
  }

  &:focus-within {
    outline: var(--segmented-input-outline-color, #5995b3)
      var(--segmented-input-outline-style, solid)
      var(--segmented-input-outline-width, #{em(2)});
    outline-offset: var(--segmented-input-outline-offset, #{em(2)});
  }

  input {
    appearance: none;
    position: absolute;

    &:enabled + label {
      cursor: pointer;
    }
  }

  &[data-variant='primary'] {
    --segmented-input-background-color: var(
      --segmented-input-primary-background-color,
      none
    );
    --segmented-input-border-color: var(--segmented-button-color, #323264);
    --segmented-input-color: var(--segmented-button-color, #323264);
    --segmented-input-hover-background-color: var(
      --segmented-button-background-hover,
      #e5e5e5
    );
    --segmented-input-hover-border-color: var(
      --segmented-button-color,
      #323264
    );
    --segmented-input-hover-color: var(--segmented-button-color, #323264);
    --segmented-input-checked-background-color: var(
      --segmented-button-background-active,
      #323264
    );
    --segmented-input-checked-color: var(
      --segmented-button-color-active,
      white
    );
  }

  &[data-variant='danger'] {
    --segmented-input-background-color: var(
      --segmented-input-danger-background-color,
      none
    );
    --segmented-input-border-color: var(
      --segmented-button-border-error,
      #e10900
    );
    --segmented-input-color: var(--segmented-button-color-error, #e10900);
    --segmented-input-hover-background-color: var(
      --delete-button-background-hover,
      #ffe6e6
    );
    --segmented-input-hover-color: var(--delete-button-hover, #aa0a0a);
    --segmented-input-hover-border-color: var(--delete-button-hover, #aa0a0a);
    --segmented-input-checked-background-color: var(
      --delete-button-background-pressed,
      #ffc2c2
    );
    --segmented-input-checked-border-color: var(
      --segmented-input-danger-checked-border-color,
      #aa0a0a
    );
    --segmented-input-checked-color: var(
      --segmented-input-danger-checked-color,
      #aa0a0a
    );
  }

  &:hover {
    --segmented-input-background-color: var(
      --segmented-input-hover-background-color
    );
    --segmented-input-border-color: var(--segmented-input-hover-border-color);
    --segmented-input-color: var(--segmented-input-hover-color);
  }

  &[data-checked='true'] {
    --segmented-input-background-color: var(
      --segmented-input-checked-background-color
    );
    --segmented-input-color: var(--segmented-input-checked-color);

    &[data-variant='danger'] {
      --segmented-input-border-color: var(
        --segmented-input-checked-border-color
      );
    }
  }

  &[data-validity='invalid'] {
    --segmented-input-background-color: var(--input-control-validity-color);
  }

  &[data-disabled='true'] {
    --segmented-input-background-color: var(
      --segmented-input-disabled-background-color,
      #a5aaaa
    );
    --segmented-input-border-color: var(
      --segmented-input-disabled-border-color,
      #a5aaaa
    );
    --segmented-input-color: var(--segmented-input-disabled-color, white);
  }
}
