html[data-theme='clover'] {
  // body
  --body-background-color: #e5e5e5;

  /**
  * @tokens Clover Primary colors
  * @presenter Color
  */

  // Primary colors
  --brand-primary: #d5431b;
  --brand-secondary: #3d1955;
  --background-1: #f5f5f5;
  --background-2: #d4eef2;

  /**
  * @tokens Clover Accent colors
  * @presenter Color
  */
  // Accent colors
  --accent-1: #0d6d5e;
  --accent-2: #026980;
  --accent-interactive: #0d6d5e;
  --accent-selected: #274b9b;

  /**
  * @tokens Clover Grayscale
  * @presenter Color
  */
  // Grayscale
  --gray-0: #f5f5f5;
  --gray-1: #dad7d7;
  --gray-2: #afaaaa;
  --gray-3: #665e5e;
  --gray-4: #3f3b3b;
  --gray-5: #2d2a2a;
  --white: #ffffff;

  /**
  * @tokens Clover Interface
  * @presenter Color
  */
  // Interface

  --interface-info: #026980;
  --interface-info-background: #eff7f7;
  --interface-success: #1c8058;
  --interface-success-background: #ebf8f3;
  --interface-warning: #c08100;
  --interface-warning-background: #fdfbf5;
  --interface-error: #cc3e19;
  --interface-error-background: #fff3f0;

  // --interface-light-grey: #f1f3f3;

  /**
  * @tokens Clover Input Colors
  * @presenter Color
  */
  --input-default: var(--gray-3); /* #606565 */
  --input-hover: var(--gray-3); /* #414646 */
  --input-focus: var(--accent-1); /* #284B9B */
  --input-disabled: var(--gray-2); /* #A5AAAA */
  --input-error: var(--interface-error); /* #E10900 */
  --input-disabled-background: var(--gray-1); /* #D9DADA */
  --toggle-check-radio: var(--interface-info); /* #284B9B */
  --toggle-inactive: var(--gray-3); /* 606565 */
  --input-button-hover: rgba(50, 50, 100, 0.08);
  --input-button-focus: var(--accent-2); /* #0082aa */
  --text-color: var(--gray-5);
  --link-color: var(--accent-interactive);
  --link-color-disabled: var(--gray-2, #a5aaaa);
  --failed-red: #e10900;
}
