@import '../utils';

$main-color: #284b9b;

@mixin input-states {
  [data-input-control-element='input'] {
    &[data-checked='true'] {
      --checkbox-input-background: var(
        --checkbox-default-input-background,
        #{$main-color}
      );

      input::after {
        content: '';
        display: block;
        height: 100%;
        width: 100%;
      }

      input[type='checkbox']::after {
        background: var(
            --checkbox-input-checkmark-background-image,
            url('./check-mark.svg')
          )
          50% 50% no-repeat;
      }

      input[type='radio'] {
        border-radius: 50%;

        &::after {
          border: em(2) solid white;
          border-radius: 50%;
          height: calc(100% - var(--checkbox-input-border-width) * 2);
          width: calc(100% - var(--checkbox-input-border-width) * 2);
        }
      }
    }
  }

  &:not([data-validity='initial']) {
    --checkbox-input-border-color: var(--input-control-validity-color);
    --input-control-label-color: var(--input-control-validity-color);

    [data-input-control-element='input'][data-checked='true'] {
      --checkbox-input-background: var(--input-control-validity-color);
    }
  }

  &[data-disabled='false'] {
    [data-input-control-element='input']:hover::before {
      opacity: 1;
    }
  }

  &[data-disabled='true'] {
    --checkbox-input-border-color: var(--input-control-validity-color);

    [data-input-control-element='input'] {
      &[data-checked='true'] {
        --checkbox-input-background: var(--input-control-validity-color);
      }
    }
  }
}

.root {
  --input-control-grid-gap: #{em(8)} #{em(12)};
  --input-control-label-font-size: 1em;

  align-items: center;
  grid-template-columns: auto 1fr;

  [data-input-control-element='input'] {
    display: grid;
    grid-template: 1fr / 1fr;

    &::before {
      border-radius: 50%;
      box-shadow: 0 0 0 em(8) rgba(50, 50, 100, 0.08);
      content: '';
      display: block;
      grid-column: 1;
      grid-row: 1;
      height: 100%;
      opacity: 0;
      pointer-events: none;
      transition: opacity 0.3s ease;
      width: 100%;
    }

    input {
      appearance: none;
      background: var(--checkbox-input-background, white);
      border-color: var(
        --checkbox-input-border-color,
        var(--checkbox-default-input-border-color, #{$main-color})
      );
      border-radius: em(4);
      border-style: solid;
      border-width: var(--checkbox-input-border-width, em(2));
      display: grid;
      grid-column: 1;
      grid-row: 1;
      height: var(--checkbox-input-height, var(--checkbox-input-width, em(24)));
      width: var(--checkbox-input-width, em(24));

      &:focus-visible {
        outline: em(2) solid var(--checkbox-input-focus-outline, #0082aa);
        outline-offset: em(2);
      }
    }
  }

  [data-input-control-element='description'] {
    grid-column: var(--checkbox-description-grid-column, 1 / 3);
  }

  [data-input-control-element='label'] {
    align-items: center;
    display: flex;
    font-weight: var(--checkbox-label-font-weight, 600);
  }

  @include input-states;
}
