@import '../../utils';

.root {
  --accordion-item-border-color: var(--accordion-border-color, #c8c8d1);

  $padding: em(4);
  $border: 1px solid var(--accordion-item-border-color);

  & [data-accordion-element='container'] {
    background-color: transparent;
    border-radius: 0;
    box-shadow: none;

    &:not(:first-child) {
      margin-top: em(26);
    }
  }

  & [data-accordion-item-element='heading'] {
    border-bottom: $border;
    border-top: $border;
    height: em(56);
  }

  & [data-accordion-panel-element='panel'] {
    border-bottom: $border;
    padding: em(16) $padding;
  }
}
