@import '../../utils';

@import './variants/primary';
@import './variants/secondary';
@import './variants/danger';
@import './variants/inverted';
@import './variants/text';
@import './variants/link';

.root {
  align-items: center;
  background-color: var(--button-background-color, none);
  border-color: var(--button-border-color, var(--button-background-color));
  border-radius: var(--button-border-radius, em(50));
  border-style: solid;
  border-width: var(--button-border-width, 1px);
  color: var(--button-color);
  display: grid;
  font-size: var(--button-font-size);
  font-weight: var(--button-font-weight, 700);
  grid-auto-flow: column;
  grid-gap: em(5) em(8);
  justify-content: var(--button-justify-content, center);
  min-width: var(--button-min-width);
  padding: var(--button-padding, #{em(13)} #{em(25)});
  transition: background-color 0.2s, color 0.2s, border-color 0.2s;
  width: var(--button-width, fit-content);

  @include variant-primary;
  @include variant-primary-outlined;
  @include variant-secondary;
  @include variant-secondary-outlined;
  @include variant-danger;
  @include variant-danger-outlined;
  @include variant-inverted;
  @include variant-inverted-outlined;
  @include variant-text;
  @include variant-link;

  svg {
    display: block;
    max-height: em(24);
    max-width: em(24);

    path {
      fill: currentColor;
    }
  }

  &[data-size='normal'] {
    --button-font-size: var(--button-normal-font-size, #{em(18)});

    line-height: var(--button-line-height, #{em(20, 18)});

    &:not([data-variant*='text']):not([data-variant='link']) {
      --button-padding: var(
        --button-normal-padding,
        #{em(14, 18) #{em(24, 18)}}
      );
    }
  }

  &[data-size='small'] {
    --button-font-size: var(--button-small-font-size, #{em(14)});

    line-height: var(--button-line-height, #{em(20, 14)});

    &:not([data-variant*='text']):not([data-variant='link']) {
      --button-padding: var(
        --button-small-padding,
        #{em(10, 14) #{em(16, 14)}}
      );
    }
  }

  &[data-size='tiny'] {
    --button-font-size: var(--button-tiny-font-size, #{em(14)});

    line-height: var(--button-line-height, #{em(20, 14)});

    &:not([data-variant*='text']):not([data-variant='link']) {
      --button-padding: var(--button-tiny-padding, #{em(6, 14) #{em(16, 14)}});
    }
  }

  &[data-size='large'] {
    --button-font-size: var(--button-large-font-size, #{em(18)});

    line-height: var(--button-line-height, #{em(20, 18)});

    &:not([data-variant*='text']):not([data-variant='link']) {
      --button-padding: var(--button-tiny-padding, #{em(11, 18) #{em(29, 18)}});
    }
  }

  &:hover {
    --button-background-color: var(--button-hover-background-color);
    --button-border-color: var(--button-hover-border-color);
    --button-color: var(--button-hover-color);
  }

  &:active {
    --button-background-color: var(--button-active-background-color);
    --button-border-color: var(--button-active-border-color);
    --button-color: var(--button-active-color);
  }

  &:focus-visible {
    outline: var(--button-outline-color, #5995b3)
      var(--button-outline-style, solid) var(--button-outline-width, #{em(2)});
    outline-offset: var(--button-outline-offset, #{em(2)});
  }

  &[data-variant*='outlined']:disabled {
    --button-disabled-background-color: var(
      --button-outlined-disabled-background-color,
      none
    );
    --button-disabled-color: var(--button-outlined-disabled-color, #a5aaaa);
  }

  &:disabled {
    --button-background-color: var(--button-disabled-background-color, #a5aaaa);
    --button-border-color: var(--button-disabled-border-color, #a5aaaa);
    --button-color: var(--button-disabled-color, white);
  }
}

a.root:not([data-variant='text']) {
  text-decoration: none;
}
