@mixin variant-secondary {
  &[data-variant='secondary'] {
    --button-background-color: var(--secondary-button-background, #c2e8ff);
    --button-color: var(--secondary-button, #323264);
    --button-hover-color: var(--secondary-button, #323264);
    --button-active-color: var(--secondary-button, #323264);
    --button-hover-background-color: var(
      --secondary-button-background-hover,
      #dbf1ff
    );
    --button-active-background-color: var(
      --secondary-button-background-pressed,
      #95d3f9
    );
  }
}

@mixin variant-secondary-outlined {
  &[data-variant='secondary-outlined'] {
    --button-background-color: var(--button-secondary-background-color, none);
    --button-border-color: var(--button-secondary-border-color, #c2e8ff);
    --button-color: var(--button-secondary-color, #323264);
    --button-hover-background-color: var(
      --button-secondary-hover-background-color,
      #dbf1ff
    );
    --button-active-background-color: var(
      --button-secondary-active-background-color,
      #95d3f9
    );
  }
}
