@mixin variant-primary {
  &[data-variant='primary'] {
    --button-background-color: var(--primary-button-background, #323264);
    --button-color: var(--primary-button, white);
    --button-hover-background-color: var(
      --primary-button-background-hover,
      #3e417a
    );
    --button-hover-color: var(--button-primary-hover-color, white);
    --button-active-background-color: var(
      --primary-button-background-pressed,
      #222248
    );
    --button-active-color: var(--button-primary-active-color, white);
  }
}

@mixin variant-primary-outlined {
  &[data-variant='primary-outlined'] {
    --button-background-color: var(
      --button-primary-outlined-background-color,
      none
    );
    --button-border-color: var(--tertiary-button, #323264);
    --button-color: var(--tertiary-button, #323264);
    --button-hover-background-color: var(
      --tertiary-button-background-hover,
      rgba(50, 50, 100, 0.08)
    );
    --button-hover-border-color: var(--tertiary-button-hover, #323264);
    --button-hover-color: var(--tertiary-button-hover, #323264);
    --button-active-background-color: var(
      --tertiary-button-background-pressed,
      #bbc7dd
    );
    --button-active-border-color: var(--tertiary-button-hover, #323264);
    --button-active-color: var(--tertiary-button-hover, #323264);
    --button-border-width: 2px;
  }
}
