@import '../utils';

.root {
  background-color: var(--avatar-background-color);
  border: var(--avatar-border);
  border-radius: var(--avatar-border-radius, 50%);
  color: var(--avatar-color);
  display: var(--avatar-display, flex);
  flex-shrink: 0;
  font-size: var(--avatar-font-size, #{em(19)});
  font-weight: var(--avatar-font-weight, 700);
  height: var(--avatar-size);
  width: var(--avatar-size);

  [data-element='image'] {
    display: block;
    height: 100%;
    object-fit: contain;
    width: 100%;
  }

  [data-element='initials'] {
    margin: auto;
    text-transform: var(--avatar-initials-text-transform, uppercase);
  }

  &[data-variant='secondary'] {
    --avatar-background-color: rgba(194, 232, 255, 1);
    --avatar-border: #{em(2)} solid rgba(0, 130, 170, 1);
    --avatar-color: rgba(50, 50, 100, 1);
  }

  &[data-variant='primary'] {
    --avatar-background-color: rgba(50, 50, 100, 1);
    --avatar-border: #{em(2)} solid rgba(50, 50, 100, 1);
    --avatar-color: white;
  }

  &[data-variant='muted'] {
    --avatar-background-color: rgba(245, 245, 240, 1);
    --avatar-border: #{em(2)} solid rgba(165, 170, 170, 1);
    --avatar-color: rgba(96, 101, 101, 1);
  }

  &[data-size='medium'] {
    --avatar-size: #{em(40, 19)};
  }
}
