html[data-theme='bsc'] {
  // body
  --body-background-color: #e5e5e5;

  /**
  * @tokens BSC Primary colors
  * @presenter Color
  */

  // Primary colors
  --brand-primary: #0071bc;
  --brand-secondary: #003b5c;
  --background-1: #f4f4f4;
  --background-2: #e6f4f9;

  /**
  * @tokens BSC Accent colors
  * @presenter Color
  */
  // Accent colors
  --accent-1: #005a96;
  --accent-2: #0082aa;
  --accent-interactive: #005a96;
  --accent-selected: #274b9b;

  /**
  * @tokens BSC Grayscale
  * @presenter Color
  */
  // Grayscale
  --gray-0: #f1f3f3;
  --gray-1: #d9dada;
  --gray-2: #a5aaaa;
  --gray-3: #606565;
  --gray-4: #414646;
  --gray-5: #25282a;
  --white: #ffffff;

  /**
  * @tokens BSC Interface
  * @presenter Color
  */
  // Interface

  --interface-info: #005a96;
  --interface-info-background: #f6f8fb;
  --interface-success: #006431;
  --interface-success-background: #f5faf7;
  --interface-warning: #de6d38;
  --interface-warning-background: #fffbf6;
  --interface-error: #e10900;
  --interface-error-background: #fef5f5;

  // --interface-light-grey: #f1f3f3;

  /**
  * @tokens BSC Input Colors
  * @presenter Color
  */
  --input-default: var(--gray-3);
  --input-hover: var(--gray-3);
  --input-focus: var(--interface-info);
  --input-disabled: var(--gray-2);
  --input-error: var(--interface-error);
  --input-disabled-background: var(--gray-1);
  --toggle-check-radio: var(--interface-info);
  --toggle-inactive: var(--gray-3);
  --input-button-hover: rgba(50, 50, 100, 0.08);
  --input-button-focus: var(--accent-2);
  --text-color: var(--gray-5);
  --link-color: var(--accent-interactive);
  --link-color-disabled: var(--gray-2, #a5aaaa);
  --failed-red: #e10900;
}
