@import '../../utils';

.root {
  --accordion-background-color: var(--white, #ffffff);
  --accordion-item-box-shadow-color: var(
    --recommendation-card-box-shadow-color,
    rgba(10, 20, 20, 0.1)
  );

  .accordion-item {
    background-color: var(--accordion-background-color);
    border-radius: em(8);
    box-shadow: 0 em(5) em(14) 0
      var(--accordion-box-shadow-color, var(--accordion-item-box-shadow-color));

    &:not(:first-child) {
      margin-top: em(24);
    }
  }
}
