@import '../utils';

$padding: em(48);

.root {
  position: relative;
  width: 100%;

  & :global(.combobox-popup-enter) {
    opacity: 0;
  }

  & :global(.combobox-popup-enter-active) {
    opacity: 1;
    transition: opacity 0.3s;
  }

  & :global(.combobox-popup-exit) {
    opacity: 1;
  }

  & :global(.combobox-popup-exit-active) {
    opacity: 0.5;
    transition: opacity 0.3s;
  }

  & :global(.combobox-popup-exit-done) {
    display: none;
  }
}

.location {
  [data-input-control-element='input'] {
    background-image: url('./assets/location.svg');
  }
}

.search {
  [data-input-control-element='input'] {
    background-image: url('./assets/searchIcon.svg');
  }
}

.text-field {
  [data-input-control-element='input'] {
    background-position: em(12);
    background-repeat: no-repeat;
    padding-left: $padding + em(12);
    padding-right: $padding;
    width: 100%;
  }
}

.input {
  position: relative;

  &::before {
    border-right: 1px solid #606565;
    box-sizing: border-box;
    content: '';
    height: 100%;
    position: absolute;
    top: 0;
    width: $padding;
  }
}

.clear-button {
  position: absolute;
  right: em(8);
  top: 50%;
  transform: translateY(-50%);
}
