@import 'uikit/utils';

.root {
  --input-control-grid-gap: #{em(8)} #{em(12)};
  --input-control-label-font-size: 1em;
  --input-control-label-font-weight: 600;
  --padding: var(--toggle-padding, #{em(2)});
  --background-color: var(--toggle-background-color, #606565);
  --checkmark-size: var(--toggle-checkmark-size, #{em(20)});
  --border-width: var(--toggle-border-width, 0);
  --border-radius: var(--toggle-border-radius, #{em(16)});
  --focus-outline: var(--toggle-focus-outline, #{em(2)} solid #0082aa);
  --checkmark-border-radius: var(
    --toggle-checkmark-border-radius,
    var(--border-radius)
  );
  --checkmark-background-color: var(--toggle-checkmark-background-color, white);
  --checked-background-color: var(--toggle-checked-background-color, #284b9b);
  --hover-box-shadow: var(
    --toggle-hover-box-shadow,
    0 0 0 #{em(8)} rgba(50, 50, 100, 0.08)
  );
  --disabled-background-color: var(--toggle-disabled-background-color, #a5aaaa);
  --description-grid-column: var(--toggle-description-grid-column, 1 / 3);

  align-items: center;
  grid-template-columns: auto 1fr;

  input {
    appearance: none;
    background-color: var(--background-color);
    border-radius: var(--border-radius);
    padding: var(--padding);
    transform: translateZ(0);
    transition: background-color 0.3s ease;
    width: calc(var(--checkmark-size) * 2 + var(--padding) * 2);

    @supports (-webkit-touch-callout: none) {
      min-height: rem(25);
    }

    &::before {
      background-color: var(--checkmark-background-color);
      border-radius: var(--checkmark-border-radius);
      content: '';
      display: block;
      height: var(--checkmark-size);
      transition: transform 0.3s ease;
      width: var(--checkmark-size);

      @supports (-webkit-touch-callout: none) {
        min-height: rem(20);
      }
    }

    &:focus-visible {
      outline: var(--focus-outline);
    }

    &[aria-checked='true'] {
      --background-color: var(--checked-background-color);

      &::before {
        transform: translateX(100%);
      }
    }

    &:disabled {
      --background-color: var(--disabled-background-color);
    }

    &:enabled:hover {
      box-shadow: var(--hover-box-shadow);
    }
  }

  [data-input-control-element='description'] {
    grid-column: var(--description-grid-column, 1 / 3);
  }

  &[data-has-label='false'] {
    grid-gap: 0;
  }
}
