@import '../utils';

.root {
  --input-border-color: var(--input-rested-border-color, #606565);

  [data-input-control-element='input'] {
    background-color: var(--input-background-color, white);
    border: 1px solid var(--input-border-color);
    border-radius: 8px;
    color: var(--input-rested-color, #0a1414);
    font-weight: 600;
    line-height: em(24);
    outline: em(1) solid transparent;
    outline-offset: 0;
    padding: em(11) em(12) em(11) em(16);
    transition: outline-color 0.3s;

    &:read-only {
      --input-background-color: var(--gray-0, #f1f3f3);
      --input-border-color: #a5aaaa;
    }

    &:focus-visible {
      &:valid {
        border-color: var(--input-outline-color);
        outline-color: var(--input-outline-color, #414646);
      }

      &:invalid {
        outline-color: var(--input-control-validity-color);
      }
    }

    &:enabled:hover {
      border-color: transparent;
      outline-color: var(--input-hover-outline-color, #414646);
      outline-offset: -1px;
      outline-width: 2px;

      &:focus-visible {
        &:valid {
          border-color: var(--input-outline-color);
          outline-color: var(--input-outline-color, #414646);
        }
      }

      --input-border-color: var(--input-outline-color, #414646);
    }
  }

  &:not([data-validity='initial']) {
    --input-border-color: var(--input-control-validity-color);
    --input-outline-color: var(--input-control-validity-color);
    --input-hover-outline-color: var(--input-control-validity-color);
  }

  &[data-disabled='true'] {
    --input-background-color: var(--gray-0, #f1f3f3);
    --input-border-color: #a5aaaa;
  }

  &[data-validity='initial']:not([data-disabled='true']) {
    [data-input-control-element='input'] {
      &:not(:placeholder-shown) {
        // --input-outline-color: var(--input-filled-border-color, #284b9b);
        // --input-border-color: var(--input-outline-color, #414646);
      }
    }
  }

  [aria-live] {
    border: 0;
    clip: rect(0, 0, 0, 0);
    -webkit-clip-path: inset(50%);
    clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    white-space: nowrap;
    width: 1px;
  }
}
