@import '../utils';

.root {
  --text-color: var(--gray-5, #0a1414);

  color: var(--primary-text-color, var(--text-color));
  font-size: rem(16);
  font-style: normal;

  &[data-text-size='tiny'] {
    font-size: rem(10);
  }

  &[data-text-size='small'] {
    font-size: rem(14);
  }

  &[data-text-size='normal'] {
    font-size: rem(16);
    letter-spacing: 0.3px;
    line-height: 1.6rem;
  }

  &[data-text-size='medium'] {
    font-size: rem(18);
  }

  &[data-text-size='large'] {
    font-size: rem(20);
  }

  &[data-text-align='center'] {
    text-align: center;
  }

  &[data-text-align='inherit'] {
    text-align: inherit;
  }

  &[data-text-align='left'] {
    text-align: left;
  }

  &[data-text-align='right'] {
    text-align: right;
  }

  &[data-text-nowrap='true'] {
    white-space: nowrap;
  }

  &[data-text-nowrap='false'] {
    white-space: normal;
  }

  &[data-text-weight='bold'] {
    font-weight: bold;
  }

  &[data-text-weight='100'] {
    font-weight: 100;
  }

  &[data-text-weight='400'] {
    font-weight: 400;
  }

  &[data-text-weight='600'] {
    font-weight: 600;
  }

  &[data-text-weight='700'] {
    font-weight: 700;
  }
}
