@import '../utils';

[data-card-element] {
  padding: em(24) em(24);

  @media screen and (min-width: 768px) {
    padding-left: em(24);
    padding-right: em(24);
  }
}

.root {
  background-color: var(--card-background-color, white);
  border-radius: var(--card-border-radius, em(8));
  box-shadow: var(--card-box-shadow, 0 em(5) em(14) rgba(10, 20, 20, 0.1));
}

[data-card-element='header'] {
  align-items: center;
  display: flex;
  justify-content: space-between;
  min-height: var(--card-header-min-height, em(76));

  &:not(:last-child) {
    border-bottom: em(1) solid
      var(--card-inner-border-color, rgba(#323264, 0.16));
  }
}

[data-card-element='section'],
[data-card-element='footer'] {
  &:not(:first-child) {
    border-top: em(1) solid var(--card-inner-border-color, rgba(#323264, 0.16));
  }
}

[data-card-element='body'] {
  [data-card-element] {
    &:last-child {
      padding-bottom: 0;
    }

    padding-left: 0;
    padding-right: 0;
  }
}
