@mixin variant-danger {
  &[data-variant='danger'] {
    --button-background-color: var(--button-danger-background-color, #e10900);
    --button-color: var(--button-danger-color, white);
    --button-hover-background-color: var(
      --button-danger-hover-background-color,
      #ffe6e6
    );
    --button-hover-color: var(--button-danger-hover-color, #aa0a0a);
    --button-hover-border-color: var(
      --button-danger-hover-border-color,
      #aa0a0a
    );
    --button-active-background-color: var(
      --button-danger-active-background-color,
      #ffc2c2
    );
    --button-active-border-color: var(
      --button-danger-active-border-color,
      #aa0a0a
    );
    --button-active-color: var(--button-danger-active-color, #aa0a0a);
  }
}

@mixin variant-danger-outlined {
  &[data-variant='danger-outlined'] {
    --button-background-color: var(--button-danger-background-color, none);
    --button-border-color: var(--button-danger-border-color, #e10900);
    --button-color: var(--button-danger-color, #e10900);
    --button-hover-background-color: var(
      --button-danger-hover-background-color,
      #ffe6e6
    );
    --button-hover-color: var(--button-danger-hover-color, #aa0a0a);
    --button-hover-border-color: var(
      --button-danger-hover-border-color,
      #aa0a0a
    );
    --button-active-background-color: var(
      --button-danger-active-background-color,
      #ffc2c2
    );
    --button-active-border-color: var(
      --button-danger-active-border-color,
      #aa0a0a
    );
    --button-active-color: var(--button-danger-active-color, #aa0a0a);
    --button-border-width: 2px;
  }
}
