.root {
  --spinner-default-size: 50px;
  --spinner-default-border-color: #323264;

  height: var(--spinner-size, var(--spinner-default-size));
  width: var(--spinner-size, var(--spinner-default-size));

  &::after {
    animation: rotate 1.2s infinite linear;
    border: 2px solid transparent;
    border-radius: 50%;
    border-top-color: var(
      --spinner-border-color,
      var(--spinner-default-border-color)
    );
    box-sizing: border-box;
    content: '';
    display: block;
    height: 100%;
    width: 100%;
  }

  &[data-position='center'] {
    margin-left: auto;
    margin-right: auto;
  }

  &[data-position='absolute-center'] {
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
