html[data-theme='bsc'] {
  --primary-button: var(--white);
  --primary-button-background: var(--brand-secondary);
  --primary-button-background-hover: #005585;
  --primary-button-background-pressed: #00263c;
  --secondary-button: var(--brand-secondary);
  --secondary-button-background: #cee9f3;
  --secondary-button-background-hover: var(--background-2);
  --secondary-button-background-pressed: #cce4ec;
  --tertiary-button: var(--brand-secondary);
  --tertiary-button-hover: var(--gray-5);
  --tertiary-button-background: var(--brand-secondary);
  --tertiary-button-background-hover: var(--interface-info-background);
  --tertiary-button-background-pressed: #dbe1eb;
  --delete-button: var(--interface-error);
  --delete-button-background: var(--interface-error);
  --delete-button-background-hover: var(--interface-error-background);
  --delete-button-hover: #aa0a0a;
  --delete-button-background-pressed: #f8d3d3;
  --disabled-button: var(--white);
  --disabled-button-background: var(--gray-2);
  --focus-button-outline-color: #5995b3;
}
