@import '../../../utils';

@mixin variant-text {
  &[data-variant='text'] {
    [data-button-element='icon'] {
      --button-font-weight: 400;
    }

    --button-padding: var(
      --button-text-padding,
      #{em(8, 18)} #{em(8, 18)} #{em(7, 18)}
    );
    --button-border-radius: var(--button-text-border-radius, #{em(8, 18)});
    --button-background-color: var(--button-text-background-color, transparent);
    --button-color: var(--accent-interactive, #284b9b);
    --button-hover-background-color: var(
      --button-text-hover-background-color,
      #e5e5e5
    );
    --button-font-size: var(--button-text-font-size, #{em(18)});
    --button-outline-offset: var(--button-text-outline-offset, 0);
    --button-hover-color: var(--accent-interactive, #284b9b);
    --button-hover-border-color: var(
      --button-text-hover-border-color,
      var(--button-text-hover-background-color)
    );
    --button-active-background-color: var(
      --button-text-active-background-color,
      #d6d6da
    );
    --button-active-border-color: var(
      --button-text-active-border-color,
      #d6d6da
    );
    --button-active-color: var(--accent-interactive, #284b9b);

    &:disabled {
      --button-background-color: var(
        --button-text-disabled-background-color,
        transparent
      );
      --button-color: var(--button-text-disabled-color, #a5aaaa);
      --button-border-color: var(
        --button-text-disabled-border-color,
        transparent
      );
    }
  }

  &[data-variant='text-inverted'] {
    [data-button-element='icon'] {
      --button-font-weight: 400;
    }

    --button-padding: var(
      --button-text-inverted-padding,
      #{em(8, 18)} #{em(8, 18)} #{em(7, 18)}
    );
    --button-border-radius: var(
      --button-text-inverted-border-radius,
      #{em(8, 18)}
    );
    --button-background-color: var(
      --button-text-inverted-background-color,
      transparent
    );
    --button-color: var(--button-text-inverted-color, white);
    --button-hover-background-color: var(
      --button-text-inverted-hover-background-color,
      #2a2a57
    );
    --button-font-size: var(--button-text-inverted-font-size, #{em(18)});
    --button-outline-offset: var(--button-text-inverted-outline-offset, 0);
    --button-hover-color: var(--button-text-inverted-hover-color, white);
    --button-hover-border-color: var(
      --button-text-inverted-hover-border-color,
      var(--button-text-inverted-hover-background-color)
    );
    --button-active-background-color: var(
      --button-text-inverted-active-background-color,
      #222248
    );
    --button-active-border-color: var(
      --button-text-inverted-active-border-color,
      #222248
    );
    --button-active-color: var(--button-text-inverted-active-color, white);

    &:disabled {
      --button-background-color: var(
        --button-text-inverted-disabled-background-color,
        transparent
      );
      --button-color: var(--button-text-inverted-disabled-color, #a5aaaa);
      --button-border-color: var(
        --button-text-inverted-disabled-border-color,
        transparent
      );
    }
  }
}
