/* stylelint-disable selector-class-pattern */
@import '~assets/styles/h3Imports';

.h3-select-override,
.h3-select__menu-portal {
  $default-border-radius: 8px;

  .h3-select__indicator-separator,
  .error__indicator-separator,
  .disabled__indicator-separator {
    display: none;
  }

  .disabled__indicator {
    svg {
      filter: grayscale(1);
    }
  }

  .h3-select__indicator {
    svg {
      fill: var(--dropdown-indicator-color, var(--accent-interactive));
    }
  }

  .h3-select__control,
  .error__control,
  .disabled__control {
    background: transparent;
    background-color: transparent;
    border: none;
    border-radius: var(--dropdown-border-radius, $default-border-radius);
    cursor: pointer;
    font-size: rem(16);
    font-weight: 600;
    height: 100%;
    min-height: rem(38);
    padding-left: 5px;
    padding-right: 10px;
    width: 100%;
  }

  .h3-select__single-value {
    color: var(--dropdown-value-color, var(--text-color));
  }

  .h3-select__control--menu-is-open,
  .error__control--menu-is-open {
    svg {
      transform: rotate(180deg);
    }
  }

  .h3-select__control {
    &.h3-select__control--menu-is-open,
    &.h3-select__control:hover {
      background-color: var(--dropdown-focus-color);
    }
  }

  .h3-select__control--is-focused {
    border-radius: 8px;
    box-shadow: 0 0 0 2px var(--input-dropdown-focus);
    height: 100%;
    width: 100%;
  }

  .error__control--is-focused {
    border-color: var(--input-error);
    box-shadow: 0 0 0 1.5px var(--input-error);
  }

  .disabled__single-value {
    color: var(--input-text-disabled);
  }

  .h3-select__menu {
    border-radius: var(--dropdown-border-radius, $default-border-radius);
    box-shadow: 0 5px 14px rgba(10, 20, 20, 0.1);

    &-list {
      padding: 0;
    }
  }

  .h3-select__option,
  .error__option {
    color: var(--text-color);
    font-size: rem(16);
    padding: 13.5px 12px;

    &:not(:first-child) {
      border-top: 1px solid var(--input-disabled);
    }

    &:last-child {
      border-bottom-left-radius: var(
        --dropdown-border-radius,
        $default-border-radius
      );
      border-bottom-right-radius: var(
        --dropdown-border-radius,
        $default-border-radius
      );
    }

    &:first-child {
      border-top-left-radius: var(
        --dropdown-border-radius,
        $default-border-radius
      );
      border-top-right-radius: var(
        --dropdown-border-radius,
        $default-border-radius
      );
    }
  }

  .h3-select__option--is-selected,
  .error__option--is-selected {
    align-items: center;
    background-color: var(--interface-info-background);
    display: flex;
    font-weight: 600;
    justify-content: space-between;

    &::after {
      color: var(--input-error);
      content: url('../../assets/icons/dropdown-check-mark.svg');
    }
  }

  .h3-select__option--is-focused {
    background-color: var(--interface-info-background);
    cursor: pointer;
    font-weight: 600;

    .arrow-key & {
      outline: 2px solid var(--focus-button-outline-color, #5995b3);
      outline-offset: -2px;
    }

    &:focus-visible {
      box-shadow: inset 0 0 0 2px var(--input-dropdown-focus);
    }

    &:hover {
      background-color: var(--interface-info-background);
      outline: none;
    }
  }
}
