@import '../utils';

.root {
  --input-control-warning-color: #e10900;
  --input-control-success-color: green;
  --input-control-disabled-color: #a5aaaa;

  display: grid;
  grid-gap: var(--input-control-grid-gap, #{em(4)} 0);

  [data-input-control-element='description'] {
    color: var(--input-control-description-color);
    font-size: var(--input-control-description-size, #{em(14)});
    font-weight: var(--input-control-description-font-weight, 600);
  }

  [data-input-control-element='label'] {
    color: var(--input-control-label-color);
    display: block;
    font-size: var(--input-control-label-font-size, #{em(14)});
    font-weight: var(--input-control-label-font-weight);
  }

  [data-input-control-element='necessity-indicator'] {
    color: var(
      --input-control-necessity-indicator-color,
      var(--input-control-warning-color)
    );
    margin: var(--input-control-necessity-indicator-margin, 0 0 0 em(5));
  }

  [data-input-control-element='input'] {
    --input-control-input-cursor: pointer;

    cursor: var(--input-control-input-cursor);
  }

  &[data-validity='invalid'] {
    --input-control-validity-color: var(
      --input-control-invalid-validity-color,
      var(--input-control-warning-color)
    );
    --input-control-description-color: var(--input-control-validity-color);
  }

  &[data-validity='valid'] {
    --input-control-validity-color: var(
      --input-control-valid-validity-color,
      var(--input-control-success-color)
    );
    --input-control-description-color: var(--input-control-validity-color);
  }

  &[data-disabled='true'] {
    [data-input-control-element='input'] {
      --input-control-input-cursor: initial;
    }

    --input-control-validity-color: var(
      --input-control-input-control-disabled-color,
      var(--input-control-disabled-color)
    );
    --input-control-label-color: var(
      --input-control-disabled-label-color,
      #606565
    );
    --input-control-description-color: var(--input-control-label-color);
    --input-control-necessity-indicator-color: var(--input-control-label-color);
  }
}
