@import '../utils';

.root {
  --textarea-border-color: var(--textarea-rested-border-color, #606565);

  grid-template-columns: 1fr auto;

  [data-textarea-element='value-limit'] {
    font-size: var(--input-control-label-font-size, #{em(14)});
    grid-column: 2;
    grid-row: 1;
  }

  [data-input-control-element='input'],
  [data-input-control-element='description'] {
    grid-column: 1 / 3;
  }

  [data-input-control-element='input'] {
    background-color: var(--textarea-background-color, white);
    border: em(1) solid var(--textarea-border-color);
    border-radius: em(8);
    color: var(--textarea-rested-color, #0a1414);
    font-weight: 600;
    line-height: 1.5;
    max-width: 100%;
    outline: em(1) solid transparent;
    outline-offset: 0;
    padding: em(11) em(12) em(11) em(16);
    resize: var(--textarea-resize);
    transition: outline-color 0.3s;

    &:read-only {
      --textarea-background-color: #d9dada;
      --textarea-border-color: #a5aaaa;
    }

    &:focus-visible {
      &:valid {
        border-color: var(--input-outline-color, #414646);
        outline-color: var(--input-outline-color, #414646);
      }

      &:invalid {
        outline-color: var(--input-control-validity-color);
      }
    }

    &[data-resize='auto'] {
      --textarea-resize: auto;
    }

    &[data-resize='none'] {
      --textarea-resize: none;
    }

    &:enabled:hover {
      border-width: 2px;
      padding: em(10) em(11) em(10) em(15);
    }
  }

  &:not([data-validity='initial']) {
    --textarea-border-color: var(--input-control-validity-color);
    --input-outline-color: var(--input-control-validity-color);
  }

  &[data-disabled='true'] {
    --textarea-background-color: #d9dada;
    --textarea-border-color: #a5aaaa;
  }

  &[data-validity='initial']:not([data-disabled='true']) {
    [data-input-control-element='input'] {
      &[data-empty='false'] {
        --input-outline-color: var(--textarea-filled-border-color, #284b9b);
        --textarea-border-color: var(--textarea-outline-color, #414646);
      }
    }
  }
}
