html[data-theme='clover'] {
  --checkbox-default-input-background: var(--accent-1);
  --checkbox-input-border-color: var(--accent-1);
  --input-focus: var(--accent-1);
  --toggle-checked-background-color: var(--accent-1);
  --datepicker-icon: var(--accent-1);
  --dropdown-icon: var(--accent-1);
  --search-outline-focus: var(--accent-1);
  --input-outline-color: var(--input-focus);
  --input-hover-outline-color: var(--input-hover);
  --textarea-outline-color: var(--input-hover);
  --input-dropdown-focus: var(--input-focus);
}
