.root {
  border-radius: var(--dropdown-border-radius, 8px);
  border-width: 0;
  display: block;
  font-weight: 400;
  height: rem(48);
  outline: 1px solid var(--input-default);
  width: 100%;

  .select-placeholder-text {
    color: var(--gray-3);
  }

  &.disabled {
    background: var(--input-disabled-background);
    border: 1px solid var(--input-disabled);
    outline: 1px solid var(--input-disabled);
  }

  &.error {
    outline: 1px solid var(--input-error);
  }

  &:valid {
    color: var(--text-color);
  }
}
